import Vue from 'vue'
import Vuex from 'vuex'
import router from "../router/index";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
        services: 'all',
        leader: ''
    },
    getters: {
        GetServices(state){
            return state.services.toLowerCase();
        },
        GetLeader(state){
            return state.leader;
        }
    },
    mutations: {
        SetServices(state, val){
            state.services = val.toLowerCase();
        },
        SetLeader(state, val){
            state.leader = val;
        }
    },
    actions: {
        _SetServices({commit}, val){
            commit('SetServices', val);
            router.push('/services');
        },
        _SetLeader({commit}, val){
            commit('SetLeader', val);
            router.push('/bio');
        }
    },
	modules: {
	}
})
