 of the Information Systems Audit and Control Association (ISACA) and is qualified in SABSA - Security Architecture. ")]),_c('p',{staticStyle:{"text-align":"justify"}},[_vm._v(" Lauritz has previously performed the role of IT Security Advisor (ITSA) and Agency Security Advisor (ASA) for Geoscience Australia, providing him great insight into Australian Government requirements. This knowledge has enabled him to undertake a wide range of government related work in the private sector including conducting secure configuration and IRAP assessments of ICT infrastructure. The assessments encompassed secure gateways & systems at a range of classification levels. Lauritz is at the forefront of the Australian Government’s transition to cloud-based services through the assessment of cloud providers and products to meet certification standards. ")]),_c('p',{staticStyle:{"text-align":"justify"}},[_vm._v(" Lauritz’s experience includes project management, security manager roles, developing disaster recovery specifications for data centres and conducting network audits against the PSPF, ISM, DSM, and ISO 27001. ")]),_c('p',{staticStyle:{"text-align":"justify"}},[_vm._v(" He has strong business communication skills and can communicate with clients at all levels within an organisation on a technical and non-technical basis and his project management skills ensure he manages time and budget effectively. ")]),_c('hr',{staticClass:"mt40 mb40 mt-xs-0 mb-xs-24"})]),_c('div',{staticClass:"col-12 d-inline-block"},[_c('div',{staticClass:"overflow-hidden pb-4 text-right"},[_c('div',{staticClass:"p2 p0-xs pt24 col-8 d-inline-block text-right"},[_c('h6',{staticClass:"uppercase mb8 number"},[_vm._v("Ben Aldridge")]),_c('h4',[_vm._v("Co-Founder & CEO | Aegis 9")])]),_c('img',{staticClass:"rounded-circle pull-left",staticStyle:{"border":"1px solid #d8d8d8"},attrs:{"alt":"Pic","width":"128","src":require("../assets/img/Ben.jpg")}})]),_c('p',{staticStyle:{"text-align":"justify"}},[_vm._v(" Ben has over 20 years’ experience in ICT with most of that time spent focussed on Security and Risk. He spent six years working in risk management, security and technical roles within the Department of Defence. Since then Ben has performed a wide range of roles incorporating project management, security manager, developing security frameworks and delivery models, cyclic review programs, cyber resiliency programs, security documentation development, and IRAP gap analysis services. ")]),_c('p',{staticStyle:{"text-align":"justify"}},[_vm._v(" Ben specialises in designing and implementing the full spectrum of security governance programs that integrate seamlessly with the broader business to realise efficiencies and generate a real time view of the organisation’s security posture. His approach is to demystify security for clients, removing the sales element from engagements and focus on developing strategic partnerships to implement robust but practical and cost-effective security solutions. ")]),_c('p',{staticStyle:{"text-align":"justify"}},[_vm._v(" Over the past 15 years, Ben has designed, delivered, and managed large Security Frameworks and Programs for Defence, ATO, the former Country Energy, and Home Affairs. He has a diverse range of professional experience, allowing the opportunity to provide logical business outcome-focused security solutions. ")]),_c('p',[_vm._v(" Ben’s core areas of expertise include governance programs, security, risk management, ISO27001, IRAP, and security service delivery models. Ben has an extensive background with Defence’s Chief Information Officer Group (CIOG), the Australian Taxation Office (ATO), and Home Affairs. ")]),_c('hr',{staticClass:"mt40 mb40 mt-xs-0 mb-xs-24"})]),_c('div',{staticClass:"col-12 d-inline-block"},[_c('div',{staticClass:"overflow-hidden pb-4"},[_c('img',{staticClass:"rounded-circle pull-left",staticStyle:{"border":"1px solid #d8d8d8"},attrs:{"alt":"Pic","width":"128","src":require("../assets/img/adele.jpg")}}),_c('div',{staticClass:"p2 p0-xs pt24 col-8 d-inline-block"},[_c('h6',{staticClass:"uppercase mb8 number"},[_vm._v("Adele Strydom")]),_c('h4',[_vm._v("CFO | Aegis 9")])])]),_c('p',{staticStyle:{"text-align":"justify"}},[_vm._v(" Adele was born in Pretoria, South Africa and commenced her part-time studies in Software Development in 1989 whilst working full-time for Solicitors. Post completion of her studies, she commenced her career as Software Developer and worked for a variety of industries in Cape Town, ranging from manufacturing to mail ordering. During 1998, Adele joined a Dutch IT company and moved to Utrecht, where she assisted large financial institutions with Y2K matters and the transition from the Gulden to the Euro. ")]),_c('p',{staticStyle:{"text-align":"justify"}},[_vm._v(" Adele migrated to Canberra, Australia in 2001. After spending a short period of time at home with her son, she was back in the workforce and joined the local solicitor’s office and later a real estate agency. In 2007, she joined an accounting firm during which time she qualified herself by obtaining a Masters in Accounting from Charles Sturt University followed by a Graduate Diploma from the Institute of Chartered Accountants. In May 2017, she started practicing full-time for her own company t/a SecFin. ")]),_c('p',{staticStyle:{"text-align":"justify"}},[_vm._v(" During this time, Adele was appointed CFO of Aegis 9. She excels in managing financial matters of the firm with ease along with swiftly handling HR matters. It gives her immense satisfaction to see the results of everyone's hard work and considers herself privileged to have worked with the team who's dedication has resulted in what Aegis 9 is today. ")]),_c('hr',{staticClass:"mt40 mb40 mt-xs-0 mb-xs-24"})]),_c('div',{staticClass:"col-12 d-inline-block"},[_c('div',{staticClass:"overflow-hidden pb-4 text-right"},[_c('div',{staticClass:"p2 p0-xs pt24 col-8 d-inline-block text-right"},[_c('h6',{staticClass:"uppercase mb8 number"},[_vm._v("Shane Edwards")]),_c('h4',[_vm._v("General Manager | Aegis 9 Security Intelligence Division")])]),_c('img',{staticClass:"rounded-circle pull-left",staticStyle:{"border":"1px solid #d8d8d8"},attrs:{"alt":"Pic","width":"128","src":require("../assets/img/avatar-m.jpg")}})]),_c('p',[_vm._v(" Shane has over 30 years’ experience in all facets of intelligence and security. Shane has operated in diverse and challenging environments throughout the world leading tactical, strategic and operational HUMINT, SIGINT, OSINT and CYBER operations. Shane holds a Bachelor of Business degree and a Master of Business Administration (MBA), along with a range of intelligence and analysis qualifications. ")]),_c('p',[_vm._v(" Shane is focussed on delivering tailored Security Intelligence solutions to clients, including training and services in Intelligence Analysis, OSINT and CYBER Threat Hunting. Aegis9 Security Intelligence also provides ICT solutions for large data-oriented clients from an Intelligence, Cyber (Security Operations and Threat Hunting) and Crime (Fraud) perspective. Shane has experience utilising the IBM i2 suite of products along with ‘best of breed’ third party plug-ins. ")]),_c('hr',{staticClass:"mt40 mb40 mt-xs-0 mb-xs-24"})]),_c('div',{staticClass:"col-12 d-inline-block"},[_c('div',{staticClass:"overflow-hidden pb-4"},[_c('img',{staticClass:"rounded-circle pull-left",staticStyle:{"border":"1px solid #d8d8d8"},attrs:{"alt":"Pic","width":"128","src":require("../assets/img/neil.jpg")}}),_c('div',{staticClass:"p2 p0-xs pt24 col-8 d-inline-block"},[_c('h6',{staticClass:"uppercase mb8 number"},[_vm._v("Neil Sunshine")]),_c('h4',[_vm._v("Director & PMO | Aegis 9")])])]),_c('p',[_vm._v(" Neil is a certified Portfolio, Program, Project (P3M) and Change Manager as well as a Maturity Assessor. He has over 30 years’ experience in ICT, with in excess of twenty years’ experience in ICT Infrastructure. Neil possesses a strong understanding of ICT within Federal Government. ")]),_c('p',[_vm._v(" As a Portfolio Manager Neil managed a team of Project and Program Managers for HP and managed the transition of the most significant transformation program undertaken in Australia, the amalgamation of the Child Support Agency, Medicare and Centrelink into the new Department of Human Services (DHS). ")]),_c('p',[_vm._v(" Neil has led change initiatives specialising in ICT security compliance and assurance roles for major organisations including Queensland State departments, NZ Health Boards and the federal government. In recent years Neil has led deliveries of Security Operations Centres (SOCs), IRAP compliance for tier 1 ICT companies and change in complex Security Architecture environments. ")]),_c('hr',{staticClass:"mt40 mb40 mt-xs-0 mb-xs-24"})]),_c('div',{staticClass:"col-12 d-inline-block"},[_c('div',{staticClass:"overflow-hidden pb-4 text-right"},[_c('div',{staticClass:"p2 p0-xs pt24 col-8 d-inline-block text-right"},[_c('h6',{staticClass:"uppercase mb8 number"},[_vm._v("Janstan Espinosa")]),_c('h4',[_vm._v("General Manager | Aegis 9 Audit and Assurance")])]),_c('img',{staticClass:"rounded-circle pull-left",staticStyle:{"border":"1px solid #d8d8d8"},attrs:{"alt":"Pic","width":"128","src":require("../assets/img/avatar-m.jpg")}})]),_c('p',[_vm._v(" Janstan holds Bachelor’s degrees in Electronics and Communications Engineering along with Actuarial Studies. He holds certifications in CISM, and CISA, and is a member of the Information Systems Audit and Control Association (ISACA). Janstan has a decade of experience in business operations and information security backed by a successful track record of dealing with various projects in complex and international environments. ")]),_c('p',[_vm._v(" Janstan has experience at the Department of Defence and as a Security Certification Consultant. These engagements have seen him work in projects that involved secure configuration and assessments of IT infrastructure including Secure Satellites, Weapon Systems, Security Operations Centres, Gateways, and IT Systems. He has also been responsible for the assessment of Cloud Services for the Australian Government using the different Cloud Service and Deployment models at a range of classification levels. ")]),_c('p',[_vm._v(" Janstan has significant expertise in security architecture, security risk assessment, security documentation development, incident response management and security operations. His roles included project management, security manager, developing disaster recovery specifications for data centres and conducting audits against the PSPF, ISM, eDSM, and ISO 27001. Janstan also has experience in setting-up and working in Security Operations Centre (SOC) and Incident Response Team (CSIRT/CERT) environments. ")]),_c('hr',{staticClass:"mt40 mb40 mt-xs-0 mb-xs-24"})]),_c('div',{staticClass:"col-12 d-inline-block"},[_c('div',{staticClass:"overflow-hidden pb-4"},[_c('img',{staticClass:"rounded-circle pull-left",staticStyle:{"border":"1px solid #d8d8d8"},attrs:{"alt":"Pic","width":"128","src":require("../assets/img/rafal.jpg")}}),_c('div',{staticClass:"p2 p0-xs pt24 col-8 d-inline-block"},[_c('h6',{staticClass:"uppercase mb8 number"},[_vm._v("Rafal Lukasiewicz")]),_c('h4',[_vm._v("General Manager | Aegis 9 Techm8")])])]),_c('p',[_vm._v(" Rafal has more than 30 years international experience in ICT security, architecture, strategies and systems delivery, covering risk assessment, data management, planning and implementation in the Defence, Government, academic and mining sectors. Rafal is a member of the Information Systems Audit and Control Association (ISACA) and holds certifications in CISM and CRISC. ")]),_c('p',[_vm._v(" Rafal holds a Diploma in Electronic Engineering and has a passion for technology. Over the years he has received certifications from vendors Microsoft, HP, Cisco, WatchGuard or VMware, just to name a few. In the past, Rafal was developing custom solutions for local and State Government agencies specialising in secure network and communication systems. More recently, Rafal was working as an IT Security Advisor (ITSA) and ICT Manager for South Australian State Government implementing PSPF, ISM and AUS/NZ 27001 standards. ")]),_c('hr',{staticClass:"mt40 mb40 mt-xs-0 mb-xs-24"})]),_c('div',{staticClass:"col-12 d-inline-block"},[_c('div',{staticClass:"overflow-hidden pb-4 text-right"},[_c('div',{staticClass:"p2 p0-xs pt24 col-8 d-inline-block text-right"},[_c('h6',{staticClass:"uppercase mb8 number"},[_vm._v("Maria Pulitano")]),_c('h4',[_vm._v("Operations Manager | Aegis 9")])]),_c('img',{staticClass:"rounded-circle pull-left",staticStyle:{"border":"1px solid #d8d8d8"},attrs:{"alt":"Pic","width":"128","src":require("../assets/img/avatar-f.jpg")}})]),_c('p',[_vm._v(" Maria is an ICT professional with 16 years’ experience providing business solutions to Federal Government Departments. Maria holds a Diploma of Leadership and Management. ")]),_c('p',[_vm._v(" Her most recent experience as a Senior Manager in Cyber Security was to develop new Cyber Security Offerings for the Federal and Enterprise market, this included an establishment of a Secure Operating Centre to the protected level. With extensive experience working in high workload ICT and Cyber Security environments, her professional history is emphasised by the level of evident people management skills, client-focused service delivery and sales expertise. ")]),_c('p',[_vm._v(" Maria has led large teams of specialised, security cleared security experts to deliver outcomes to clients and has experience in transitioning large workforces to new engagements. ")]),_c('hr',{staticClass:"mt40 mb40 mt-xs-0 mb-xs-24"})])])])])}]

