import Vue from 'vue'
import App from './App.vue'
import store from './store'
import router from './router'

import jQuery from 'jquery'

window.jQuery = window.jQuery = jQuery;

import '../public/js/flexslider'
//import '../public/js/parallax'

import '../public/js/scripts'

Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
      setTimeout(() => {
        jQuery('.background-image-holder').each(function() {
            var imgSrc = jQuery(this).children('img').attr('src');
            jQuery(this).css('background', 'url("' + imgSrc + '")');
            jQuery(this).children('img').hide();
            jQuery(this).css('background-position', 'initial');
        });

        jQuery('.background-image-holder').each(function() {
            jQuery(this).addClass('fadeIn');
        });

        // Image Sliders
            if(jQuery('.slider-all-controls, .slider-paging-controls, .slider-arrow-controls, .slider-thumb-controls, .logo-carousel').length){
            jQuery('.slider-all-controls').flexslider({
                start: function(slider){
                    if(slider.find('.slides li:first-child').find('.fs-vid-background video').length){
                        slider.find('.slides li:first-child').find('.fs-vid-background video').get(0).play(); 
                    }
                },
                after: function(slider){
                    if(slider.find('.fs-vid-background video').length){
                        if(slider.find('li:not(.flex-active-slide)').find('.fs-vid-background video').length){
                            slider.find('li:not(.flex-active-slide)').find('.fs-vid-background video').get(0).pause();
                        }
                        if(slider.find('.flex-active-slide').find('.fs-vid-background video').length){
                            slider.find('.flex-active-slide').find('.fs-vid-background video').get(0).play();
                        }
                    }
                }
            });
            jQuery('.slider-paging-controls').flexslider({
                animation: "slide",
                directionNav: false
            });
            jQuery('.slider-arrow-controls').flexslider({
                controlNav: false
            });
            jQuery('.slider-thumb-controls .slides li').each(function() {
                var imgSrc = jQuery(this).find('img').attr('src');
                jQuery(this).attr('data-thumb', imgSrc);
            });
            jQuery('.slider-thumb-controls').flexslider({
                animation: "slide",
                controlNav: "thumbnails",
                directionNav: true
            });
            jQuery('.logo-carousel').flexslider({
                minItems: 1,
                maxItems: 4,
                move: 1,
                itemWidth: 200,
                itemMargin: 0,
                animation: "slide",
                slideshow: true,
                slideshowSpeed: 3000,
                directionNav: false,
                controlNav: false
            });
        } 
    }, 10);
    
    // Tabbed Content

    setTimeout(() => {
        jQuery('.tabbed-content').each(function() {
            jQuery(this).append('<ul class="content"></ul>');
        });
    
        jQuery('.tabs li').each(function() {
            var originalTab = jQuery(this),
                activeClass = "";
            if (originalTab.is('.tabs>li:first-child')) {
                activeClass = ' class="active"';
            }
            var tabContent = originalTab.find('.tab-content').detach().wrap('<li' + activeClass + '></li>').parent();
            originalTab.closest('.tabbed-content').find('.content').append(tabContent);
        });
    
    }, 10);

    setTimeout(() => {
        jQuery('.tabs li').click(function() {
            jQuery(this).closest('.tabs').find('li').removeClass('active');
            jQuery(this).addClass('active');
            var liIndex = jQuery(this).index() + 1;
            jQuery(this).closest('.tabbed-content').find('.content>li').removeClass('active');
            jQuery(this).closest('.tabbed-content').find('.content>li:nth-of-type(' + liIndex + ')').addClass('active');
        });
    }, 100);
    
    window.scrollTo(0, 0);

    
           
    next();    
    //}, 200);
});

new Vue({
  store,
  router,
  jQuery,
  render: h => h(App)
}).$mount('#app')


